<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">Picker</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text"></div>
            </li>
            <li>
                <h3>Demo</h3>
                <p class="btn" @click.stop="picker1()">demoData1</p>
                <p class="btn" @click.stop="picker2()">demoData2</p>
                <p class="btn" @click.stop="picker3()">demoData3</p>
                <div class="text">
                    <input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Function</h3>
<pre class="JS">
// 
let cityData = {
    "Code": "310000",
    "Name": "",
    "level": [
        {
            "Code": "310000",
            "Name": "",
            "sort": 1,
            "level": [
                {
                    "Code": "312000",
                    "Name": "",
                    "sort": 1
                }
            ]
        }
    ]
}
</pre>

<pre class="JS">
// 
new Picker({
    //()
    "title": '',

    //
    "defaultValue": self.nowVal,

    //[123]
    //,3
    "type": 1,

    //()
    "data": cityData,

    //[idtextdata]()
    "keys": {
        "id": "Code",
        "value": "Name",
        "childData": "level"
    },

    "callBack": function (val) {
        //val
        self.nowVal = val;
    }
});
</pre>

            </li>
            <li>
                <h3>Usage</h3>
                <div class="text">
                    </br>
                    'picker/picker.css'</br>
                    'picker/picker.min.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Picker from '../../lib/picker/picker'
    import cityData from '../../lib/picker/city.js'

	export default {
		name: 'picker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 
            picker1: function () {
                var self = this;
                new Picker({
                    "title": '',//()
                    "defaultValue": self.nowVal,//
                    "type": 1,//[123],3
                    "data": cityData,//()
                    "keys": {
                        "id": "Code",
                        "value": "Name",
                        "childData": "level"//3
                    },//(idtextdata)
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            },

            picker2: function () {
                var self = this;
                new Picker({
                    "title": '',//()
                    "defaultValue": self.nowVal,//-
                    "type": 2,//[123]
                    "data": cityData,//()
                    "keys": {
                        "id": "Code",
                        "value": "Name",
                        "childData": "level"//3
                    },//(idtextdata)
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            },

            picker3: function () {
                var self = this;
                new Picker({
                    "title": '',//()
                    "defaultValue": self.nowVal,//-
                    "data": cityData,//()
                    "keys": {
                        "id": "Code",
                        "value": "Name",
                        "childData": "level"//3
                    },//(idtextdata)
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    /*app.vue */
    /*@import "../../lib/datePicker/picker.css";*/
</style>


